:root {
  --content-width: 75rem;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
  text-decoration: none;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

@font-face {
  font-family: "GTAmerica-Medium";
  src: url("../../fonts/GT-America-Medium.OTF") format("opentype");
}
@font-face {
  font-family: "GTAmerica-Regular";
  src: url("../../fonts/GT-America-Regular.OTF") format("opentype");
}
@font-face {
  font-family: "GTAmerica-RegularItalic";
  src: url("../../fonts/GT-America-Regular-Italic.OTF") format("opentype");
}
html {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "GTAmerica-Medium", Helvetica, Arial, serif;
  font-weight: 100;
  line-height: 1;
}

strong, b {
  font-family: "GTAmerica-Medium", Helvetica, Arial, serif;
  font-weight: 100;
}

img {
  max-width: 100%;
}

header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  width: 100%;
  top: 0;
  background: #fff;
  margin: 0 6em;
  padding: 30px 0;
}

.page {
  padding: 5vh 5vw 10vh;
  margin: 0 auto;
  max-width: var(--content-width);
}
.page .sticky-headline {
  position: absolute;
  display: block;
  bottom: 40px;
  left: 0;
  right: 0;
  text-align: center;
  font-family: "GTAmerica-Medium", Helvetica, Arial, serif;
  font-size: 34px;
  color: #212020;
  letter-spacing: -1px;
}

main {
  position: relative;
}
main .hero-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin-top: -5vh;
}
main .hero-wrapper img {
  width: auto;
  max-height: 60vh !important;
}